.containerHeader {
    color: white;
    background-image: url(/public/workmasterImages/backgroundTop.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 1100px;
  }
  
  
  
  
  header {
    max-width: 1800px;
    margin-left: auto;
    margin-right: auto;
    padding: 100px 10px;
  }
  
  
  
  .header {
  
  
    color: white;
  }
  
  
  
  hr {
    margin-top: 30px;
    opacity: 0.2;
  }
  
  
  
  
  .header__menu {
    display: flex;
    justify-content: space-between;
  }
  
  
  
  .header__menu--nav--item {
    display: flex;
  }
  
  .header__menu--nav--list {
    margin-left: 30px;
  }
  
  
  .header__MainText {
    margin-top: 60px;
    font-size: 55px;
    line-height: 55px;
    /* or 58px */
  
  
    background: radial-gradient(44.4% 201.32% at 52.91% 53.23%, #C292FF 0%, #FFFFFF 100%)
      /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */
    ;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    max-width: 840px;
    height: 250px;
  }
  
  h1{
    font-weight: 400;
  }
  
  .header__SubText {
  
    font-size: 14px;
    line-height: 23px;
    letter-spacing: 0.1em;
    height: 100px;
  
    background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 153.26%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  
  
    opacity: 0.5;
    width: 650px;
  }
  
  .FormContainer {
    margin-top: -20px;
    border: 1px solid rgb(255, 0, 238);
    max-width: 400px;
    border-radius: 20px;
    padding: 30px;
    align-items: end;
    text-align: end;
  }
  
  .form-group {
    margin-top: 30px;
  }
  
  .form-group textarea {
    max-width: 100%;
  }
  
  
  
  .btn-form {
    background-color: transparent;
    border: 2px solid #DC4BF2;
    padding: 28px 132px 28px 38px;
    position: relative;
    border-radius: 100px;
    color: white;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  
  .btn-form:hover {
    transition: background-color 0.3s ease;
    box-shadow: 0 0 3px 2px #DC4BF2;
    transition: box-shadow 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .btn-form::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-50%);
    width: 72px;
    height: 72px;
    background-color: #6E2881;
    border-radius: 50%;
  }
  
  .btn-form::after {
    content: "\2713";
    /* Код символа галочки */
    position: absolute;
    text-decoration: none;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    color: #FFF;
    font-size: 30px;
    font-weight: bold;
  }
  
  
  #contact-btn {
    margin-top: 20px;
  }
  
  
  
  