.containerProgramming {
    color: white;
    background-image: url(/public/workmasterImages/programmingBackground.jpg);
    background-size: cover;
  background-repeat: no-repeat;
    height: 1000px;
}



.programmingSection{
    max-width: 1400px;
    padding: 100px;
    float: left;
    margin-left: 85px;

}

.programmingsectionText{
    margin-top: 200px;
    
}



#programmingsection_maintext {
    font-size: 40px;
    line-height: 120%;
    /* identical to box height, or 58px */
    align-items: center;
    text-align: center;

    background: radial-gradient(44.4% 201.32% at 52.91% 53.23%, #C292FF 0%, #FFFFFF 100%)
        /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */
    ;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}


#programmingsection_subtext {
    margin-top: 50px;
    font-size: 24px;
    line-height: 28px;
    align-items: center;
    text-align: center;
    color: #FFFFFF;

}

#programmingsection_subtext-services {
    margin-top: 50px;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.1em;

    background: linear-gradient(180deg, #FFFFFF 50%, rgba(255, 255, 255, 0) 263.59%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    align-items: center;
    text-align: center;
    opacity: 0.5;

}



.programmingsection-btn {
    margin-top: 50px;
    text-align: center;
    align-items: center;
    transition: 0.3s ease;

}

.programmingsection-btn button:hover {
    box-shadow: 0 0 3px 2px #DC4BF2;
    transition: box-shadow 0.3s ease-in-out;
    cursor: pointer;
    transition: 0.3s ease;

}