@font-face {
  font-family: 'Benzin';
  src: local('Benzin Regular'), local('Benzin-Regular'),
    url('Benzin-Regular.woff2') format('woff2'),
    url('Benzin-Regular.woff') format('woff'),
    url('Benzin-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Benzin';
  src: local('Benzin Semibold'), local('Benzin-Semibold'),
    url('Benzin-Semibold.woff2') format('woff2'),
    url('Benzin-Semibold.woff') format('woff'),
    url('Benzin-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}




body {
    font-family: 'Benzin';
    background-color:#1F1033 ;
    width: 100%;
    margin: 0;
}


/* CSS для кнопки "Выбрать файл" */
input[type="file"]::-webkit-file-upload-button {
  padding: 5px;
  background-color: transparent;
  border-radius: 5px;
  color: #ffffff;
  border: 1px solid white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

input[type="file"]::-webkit-file-upload-button:hover {
  background-color: purple;
}

input[type="file"]::-webkit-file-upload-button:focus {
  outline: none;
}


button {
  background-color: transparent;
  border: 2px solid #DC4BF2;
  padding: 10px 40px;
  position: relative;
  border-radius: 100px;
  color: white;
  transition: box-shadow 0.3s ease-in-out;
}

button:hover {
  box-shadow: 0 0 3px 2px #DC4BF2;
  transition: box-shadow 0.3s ease-in-out;
  cursor: pointer;
  transition: background-color 0.3s ease;

}

textarea {
  resize: none;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  width: 400px;
  color: white;

}

input:focus {
  outline: none;
}

textarea:focus {
  outline: none;
}



input {
  color: white;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  background-color: transparent;
  width: 400px;
  padding: 5px 0px;
}

li,
ul {
  list-style: none;
}


a {
  text-decoration: none;
  color: white;
}

img {
  width: 40px;
  height: 40px;
}


h1 {
  font-size: 25px;
}


