.containerDesign {
    color: white;
    background-image: url(/public/workmasterImages/designBackground.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 1000px;

}
  


.designSection{
    max-width: 1400px;
    padding: 100px;
    float: right;
    margin-right: 20px;
}

.designsectionText{
    margin-top: 200px;
    
}



