.containerReviews {
    color: white;
    background-image: url(/public/workmasterImages/reviewsBackground.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 900px;


}


section {
    max-width: 1400px;
    margin: 0 auto;
    padding: 100px 100px 0px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  




.reviewssection__MainText
{
    margin-top: 100px;
    font-size: 40px;
line-height: 120%;
/* identical to box height, or 58px */
align-items: center;
text-align: center;

background: radial-gradient(44.4% 201.32% at 52.91% 53.23%, #C292FF 0%, #FFFFFF 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
text-align: center;
}

.reviewssection__SubText-services {
  margin-top: 30px;
  text-align: center;
}




.reviews__menu{
    display: flex;
    float: none;
}

.reviews__menu--list{
    margin-left: 30px;
    font-size: 16px;
    padding: 20px 10px;

  background: rgba(220, 75, 242, 0.2);
    border-radius: 50px;
    margin-bottom: 50px;
    box-shadow:  0px 0px 30px #e413ff;

}



.tglink a
{
    text-decoration: none;
            transition: 0.5s;
             padding: 20px;
        border-radius: 100px;
        border: 3px solid #DC4BF2;

}

.tglink a:hover
{

    color:white;
    text-decoration: none;
        box-shadow: 10px -10px 50px #DC4BF2;
        transition: 0.5s;
        padding: 10px;
        border-radius: 100px;
        background-color: #DC4BF2;
}
