/* Navbar.css */

body{
    font-weight: 300;
    font-size: 15px;
}
.navbar-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
    display: none;
  }
  
  nav {
    width: 100%;
    height: 5em;
    background-color:transparent;
    display: flex;
    justify-content: flex-start;
    padding: 40px ;
  }
  
  .burger-menu {
    height: 100%;
    width: 4em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    cursor: pointer;
    gap: 5px;
  }
  
  .burger-bar {
    width: 30px;
    height: 2px;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
  }
  
  .menu {
    width: 50%;
    height: 100vh;
    background-color: rgb(61, 3, 90);
    position: absolute;
    top: 0;
    z-index: -1;
  }
  
  .hidden {
    display: none;
  }
  
  .visible {
    display: inherit;
  }
  
  .burger-bar.clicked{
    font-size: 6px;
  }

  .burger-bar.clicked:nth-child(1) {
    transform: rotate(45deg) translate(0.75em, 1.25em);
    transition: ease-out 0.5s;
  }
  
  .burger-bar.clicked:nth-child(2) {
    transform: scale(0);
    transition: ease-out 0.5s;
  }
  
  .burger-bar.clicked:nth-child(3) {
    transform: rotate(135deg) translate(-0.5em, 1em);
    transition: ease-out 0.5s;
  }
  
  .burger-bar.unclicked {
    transform: rotate(0) translate(0);
    transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
  }
  

  .brgmenu{

    display: block;
  }

  .brgmenu--list{
    margin-top: 100px;
  }

  .brgmenu--list img{
    width: 20px;
    height: 20px;
  }

@media (max-width:600px){
    .navbar-container {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 9999;
        display: flex;
      }
      
}